<template>
    <form-item-wrapper
        :designer="designer"
        :field="field"
        :rules="rules"
        :design-state="designState"
        :parent-widget="parentWidget"
        :parent-list="parentList"
        :index-of-parent-list="indexOfParentList"
        :sub-form-row-index="subFormRowIndex"
        :sub-form-col-index="subFormColIndex"
        :sub-form-row-id="subFormRowId">
        <el-input
            ref="fieldEditor"
            v-model="fieldModel"
            :disabled="field.options.disabled"
            :readonly="field.options.readonly"
            :size="widgetSize"
            class="hide-spin-button"
            :type="inputType"
            :show-password="field.options.showPassword"
            :placeholder="field.options.placeholder"
            :clearable="field.options.clearable"
            :minlength="field.options.minLength"
            :maxlength="field.options.maxLength"
            :show-word-limit="field.options.showWordLimit"
            :prefix-icon="field.options.prefixIcon"
            :suffix-icon="field.options.suffixIcon"
            @focus="handleFocusCustomEvent"
            @blur="handleBlurCustomEvent"
            @input="handleInputCustomEvent"
            @change="handleChangeEvent">
            <template #append v-if="field.options.appendButton">
                <el-button
                    :disabled="field.options.disabled || field.options.appendButtonDisabled"
                    @click="emitAppendButtonClick"><svg-icon :icon-class="field.options.buttonIcon" /></el-button>
            </template>
        </el-input>
    </form-item-wrapper>
</template>

<script>
import FormItemWrapper from './form-item-wrapper'
import emitter from '@/utils/emitter'
import i18n, { translate } from '@/utils/i18n'
import fieldMixin from '@/components/form-designer/form-widget/field-widget/fieldMixin'

export default {
    name: 'InputWidget',
    componentName: 'FieldWidget', // FieldWidgetbroadcast
    mixins: [emitter, fieldMixin, i18n],
    props: {
        field: Object,
        parentWidget: Object,
        parentList: Array,
        indexOfParentList: Number,
        designer: Object,

        designState: {
            type: Boolean,
            default: false
        },

        subFormRowIndex: { /* 0 */
            type: Number,
            default: -1
        },
        subFormColIndex: { /* 0 */
            type: Number,
            default: -1
        },
        subFormRowId: { /* Idid */
            type: String,
            default: ''
        }

    },
    components: {
        FormItemWrapper
    },
    data() {
        return {
            oldFieldValue: null, // fieldchange
            fieldModel: null,
            rules: []
        }
    },
    computed: {
        inputType() {
            if (this.field.options.type === 'number') {
                return 'text' // inputtypenumberv-model
            }

            return this.field.options.type
        }

    },
    beforeCreate() {
        /*  */
    },

    created() {
        /* mountedcreatedmountedmountedprop
         created */
        this.initFieldModel()
        this.registerToRefList()
        this.initEventHandler()
        this.buildFieldRules()

        this.handleOnCreated()
    },

    mounted() {
        this.handleOnMounted()
    },

    beforeUnmount() {
        this.unregisterFromRefList()
    },

    methods: {

    }
}
</script>

<style lang="scss" scoped>
  @import "../../../../styles/global.scss"; /* form-item-wrapper */

</style>
